<template>
<Navbar />
<div class="flex flex-col h-screen justify-center items-center">
    <div v-if="isFinish == true" class="flex flex-col gap-4">
        <div>
            <h1 class="text-xl font-bold">Kandidat yang kamu pilih</h1>
        </div>
        <div class="flex gap-5">
            <div class="flex flex-col gap-3 rounded-lg shadow-xl p-4" v-for="selectedCandidate in selectedCandidates" v-bind:key="selectedCandidate.category_name">
                <div class="font-bold">{{ selectedCandidate.category_name }}</div>
                <div class="font-bold">{{ selectedCandidate.candidate.no_urut }}</div>
                <div><img v-bind:src="selectedCandidate.candidate.candidate_profile_picture" width="200" height="200" alt="" srcset=""></div>
                <div class="font-bold">{{ selectedCandidate.candidate.full_name }}</div>
                <div>{{ selectedCandidate.candidate.kelas }}</div>
            </div>
        </div>
        <div class="w-full gap-4">
            <div class="w-1/2 inline-block p-4">
                <button class="border-2 border-red-600 w-full p-2 rounded-lg text-red-600 hover:bg-red-600 hover:text-white" v-on:click="back()">Ganti Pilihan</button>
            </div>
                
            <div class="w-1/2 inline-block p-4">
                <button class="border-2 border-red-600 w-full p-2 rounded-lg text-red-600 hover:bg-red-600 hover:text-white" v-on:click="submit()">Submit</button>
            </div>
        </div>
    </div>
    <div class="h-screen w-screen fixed bg-white flex flex-col z-10 inset-0 justify-center items-center" v-if="isSubmittedSuccess == true" >
        <div class="fixed inset-0 bg-black bg-opacity-60"></div>
        <div class="z-10 flex flex-col gap-4 text-xl bg-white w-1/4 rounded-lg relative h-3/4 p-8 justify-center items-center">
            <div class="text-2xl text-green-500"><icon size="6x" :icon="['fas', 'check']" /></div>
            <div class="font-bold">Terima kasih suara anda masuk</div>
            <div><button class="border-4 border-red-600 text-red-600 font-bold px-4 py-2 hover:bg-red-600 hover:text-white" @click="logout()">Logout</button></div>
        </div>
        
    </div>
    <div class="h-screen w-screen fixed bg-white flex flex-col z-10 inset-0 justify-center items-center" v-if="isSubmittedError == true" >
        <div class="fixed inset-0 bg-black bg-opacity-60"></div>
        <div class="z-10 flex flex-col gap-4 text-xl bg-white w-1/4 rounded-lg relative h-3/4 p-8 justify-center items-center">
            <div class="text-2xl text-red-600"><icon size="6x" :icon="['fas', 'times']" /></div>
            <div class="font-bold">Anda sudah voting</div>
            <div><button class="border-4 border-red-600 text-red-600 font-bold px-4 py-2 hover:bg-red-600 hover:text-white" @click="logout()">Logout</button></div>
        </div>
        
    </div>
    <div v-if="isFinish == false">        
        <div class="text-xl">Pilih kandidat</div>
        <div class="font-bold text-xl">{{ categories[currentPage].category_name }}</div>
        <div class="p-4">
            <div class="flex gap-3 width-auto">
                <div class="flex flex-col" v-for="candidate in candidates[categories[currentPage].id]" v-bind:key="candidate.id">
                        <div class="flex flex-col gap-2 p-3 cursor-pointer justify-items-center shadow-xl rounded-t-lg" style="width: 200px" v-bind:class="getClass(candidate)" v-on:click="select(candidate)" v-bind:id="candidate.id">
                            <div class="font-bold">{{ candidate.no_urut }}</div>
                            <div><img v-bind:src="candidate.candidate_profile_picture" alt="" width="200" height="200" srcset=""></div>
                            <div class="font-bold">{{ candidate.full_name }}</div>
                            <div>{{ candidate.kelas }}</div>
                        </div>
                        <div class="p-2 shadow-xl rounded-b-lg bg-white border-t-2 hover:bg-blue-600 border-white-100 cursor-pointer hover:text-white" @click="show(candidate)">
                            <icon :icon="['fas', 'bars']" />
                        </div>
                        <div id="modal" class="z-10 fixed inset-0" v-if="candidate.modalShow">
                            <div class="flex h-screen justify-center items-center">

                                <div class="fixed inset-0 bg-black bg-opacity-60"></div>
                                <div class="z-10 bg-white w-3/4 rounded-lg max-h-[80%] relative">
                                    <button class="absolute right-3 top-3" @click="close(candidate)">X</button>
                                    <div class="flex p-4 gap-4">
                                        <div><img v-bind:src="candidate.candidate_profile_picture" width="200" alt="" srcset=""></div>
                                        <div class="text-left flex flex-col gap-2">
                                            <div class="font-bold">Nama</div>
                                            <div>{{ candidate.full_name }}</div>
                                            <div class="font-bold">Kelas</div>
                                            <div>{{ candidate.kelas }}</div>
                                            <div class="font-bold">Visi</div>
                                            <div>{{ candidate.visi }}</div>
                                            <div class="font-bold">Misi</div>
                                            <div>{{ candidate.misi }}</div>
                                        </div>
                                        
                                    </div>
                                </div>
                            </div>
                        </div>
                </div>
            </div>
        </div>
        <div class="w-full gap-4">
            <div class="w-1/2 inline-block p-4">

                <button class="w-full p-2 rounded-xl border-blue-700 border-2 text-blue-700 hover:bg-blue-700 hover:text-white" v-if="currentPage >= minPage && currentPage > minPage" v-on:click="previous()"><icon :icon="['fas', 'angle-left']" /></button>
            </div>
                
            <div class="w-1/2 inline-block p-4">
                <button class="w-full p-2 rounded-xl border-blue-700 border-2 text-blue-700 hover:bg-blue-700 hover:text-white" v-if="currentPage <= maxPage && currentPage < maxPage" v-on:click="next()"><icon :icon="['fas', 'angle-right']" /></button>
                <button class="w-full p-2 rounded-xl border-blue-700 border-2 text-blue-700 hover:bg-blue-700 hover:text-white" v-if="currentPage == maxPage && categories.length == votedCandidates.data.length" v-on:click="finish()"><icon :icon="['fas', 'angle-right']" /></button>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import Navbar from '../components/Navbar.vue'
export default {
    name: "VoterPage",
    components: {
        Navbar
    },
    data(){
        
        return {
            candidates: [],
            categories: [],
            votedCandidates: {
                data: []
            },
            isSubmittedSuccess: false,
            isSubmittedError: false,
            selectedCandidates:[],
            currentPage: 0,
            minPage: 0,
            maxPage: 0,
            isFinish: false
        }
    },
    created(){
        if(this.candidates.length < 1){
            this.$axios.get("/kategori/candidates").then((data) => {
                if(data.data.status == "ok"){
                    this.candidates = data.data.data
                    Object.keys(this.candidates).map((value, index) => {
                        console.log(index)
                        this.candidates[value].map((v, i) => {
                            console.log(i)
                            v.selected = false
                            v.modalShow = false
                            return v
                        })
                    })
                    console.log(this.candidates)
                }
            })
        }
        if(this.categories.length < 1){
            this.$axios.get("/kategori").then((data) => {
                if(data.data.status == "ok"){
                    this.categories = data.data.data
                    this.maxPage = this.categories.length - 1
                    this.minPage = 0;
                }
            })
        }
        
        
    },
    methods:{
        getClass(candidate){
            const key = this.candidates[candidate.category_id].findIndex(val => val.id == candidate.id) 
            if (this.candidates[candidate.category_id][key].selected){
                console.log(true)
                return "bg-blue-100 border-4 border-blue-200"
            }
            return "group border-4 border-white hover:bg-blue-100 hover:border-blue-200"
        },
        show(candidate){
            let index = this.candidates[candidate.category_id].findIndex(val => val.id == candidate.id)
            this.candidates[candidate.category_id][index].modalShow = true
        },
        close(candidate){
            let index = this.candidates[candidate.category_id].findIndex(val => val.id == candidate.id)
            this.candidates[candidate.category_id][index].modalShow = false
        },
        select(candidate){
            if(candidate.selected == false){
                this.candidates[candidate.category_id].map((value, index) => {
                    console.log(index)
                    value.selected = false
                    return value
                });
                const paramIndex = this.votedCandidates.data.findIndex(val => val.category_id == candidate.category_id)
                if(paramIndex != -1){
                    this.votedCandidates.data[paramIndex].candidate_id = candidate.id
                }else{
                    this.votedCandidates.data.push({
                        category_id: candidate.category_id,
                        candidate_id: candidate.id, 
                    })
                }
                let index = this.candidates[candidate.category_id].findIndex(val => val.id == candidate.id)
                this.candidates[candidate.category_id][index].selected = true
            }else{
                let index = this.candidates[candidate.category_id].findIndex(val => val.id == candidate.id)
                this.candidates[candidate.category_id][index].selected = false

                const paramIndex = this.votedCandidates.data.findIndex(val => val.category_id == candidate.category_id)
                this.votedCandidates.data.splice(paramIndex, 1)


            }
                console.log(this.votedCandidates)


        },
        next(){
            if(this.currentPage != this.maxPage){
                this.currentPage = this.currentPage + 1
            }
        },
        previous(){
            if(this.currentPage != this.minPage){
                this.currentPage = this.currentPage - 1
            }
        },
        submit(){
            const token = localStorage.getItem("jwt");
            this.$axios.post("/vote", this.votedCandidates, { headers: {"Authorization" : `Bearer ${token}`}}).then((data) => {
                console.log(data)
                if(data.data.status == "ok"){
                    this.isSubmittedSuccess = true
                    this.isSubmittedError = false
                }else{
                    this.isSubmittedError = true
                    this.isSubmittedSuccess = false
                }
            })
        },
        finish(){
            this.isFinish = true
            console.log(this.votedCandidates.data)
            this.selectedCandidates = []
            this.votedCandidates.data.forEach((val, index) => {
                console.log(index)
                this.selectedCandidates.push({
                    category_name: this.categories[this.categories.findIndex(_val => _val.id == val.category_id)].category_name,
                    candidate: this.candidates[val.category_id][this.candidates[val.category_id].findIndex(_val => _val.id == val.candidate_id)]
                })
                console.log(val.category_id, val.candidate_id)
                console.log(this.candidates[val.category_id])
            });
        },
        back(){
            this.isFinish = false
        },
        logout(){
            localStorage.removeItem("jwt")
            this.$router.push("/")
        }
    }
}
</script>

