import { createRouter, createWebHistory } from 'vue-router'
import LoginPage from '../views/LoginPage.vue'
import VoterPage from '../views/VoterPage.vue'
import VoterVotingPage from '../views/VoterVotingPage.vue'
import Admin from '../views/Admin.vue'

const routes = [
  {
    path: '/',
    name: 'LoginPage',
    component: LoginPage
  },
  {
    path: '/voter',
    name: "Voter",
    meta: {
      auth: true,
      role: "voter"
    },
    component: VoterPage,
    children: [
      {
        path:":kategori_id",
        component: VoterVotingPage
      }
    ]
  },
  {
    path: '/admin',
    name: 'About',
    component: Admin,
    meta: {
      auth: true,
      role: "admin"
    }
  }
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
  //}
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})
router.beforeEach((to, from, next) => {
  const jwt = localStorage.getItem("jwt")
  if(to.matched.some(record => record.meta.auth)){
    if( jwt == null){
      console.log(1234)
      next("/")
    } else {
      if(to.matched.some(record => record.meta.role)){
        let payload = jwt.split(".")[1]
        payload = atob(payload)
        payload = JSON.parse(payload).user_data
        console.log(payload.role == to.meta.role)
        if(payload.role == to.meta.role){
          next()
          console.log(router.currentRoute)
        }else{
          next("/403")
        }
      }else{
        next()
      }
    }
  }else{
    next()
  }
})
export default router
