<template>
  <div class="w-full h-screen flex justify-center items-center" v-on:keyup.enter="login()">
    <div class="z-10 flex flex-col space-y-4 p-5 py-8 shadow-xl rounded-lg bg-white text-left w-1/4">
      <div>
        <h1 class="font-bold text-2xl">Login</h1>
        <span class="text-red-600">{{ error }}</span>
        
      </div>
      <div class="flex flex-col gap-4">
        <div class="font-bold">
          NISN
        </div>
        <div>
          <input type="text" v-model="nisn" class="border-2 border-gray-200	rounded-lg px-4 py-2 w-full">
        </div>
      </div>

      <div class="flex flex-col gap-4">
        <div class="font-bold">
          NIS
        </div>
        <div>
          <input type="text" v-model="nis" class="border-2 border-gray-200 rounded-lg px-4 py-2 w-full">
        </div>
      </div>
      <div>
        <button class="bg-blue-600 w-full py-2 rounded-lg text-white" v-on:click="login()" >Masuk</button>
      </div>
    </div>
    <img src="/voting.svg" class="z-0 absolute w-1/2 left-0 bottom-0" alt="" srcset="">
  </div>
</template>

<script>
// eslint-disable-next-line no-unused-vars
export default {
  name: 'LoginPage',
  props: {
    msg: String
  },
  data(){
      return {
        username: "",
        password: "",
        error: ""
    }
  },
  created(){
        const jwt = localStorage.getItem("jwt")
        if(jwt != null && jwt != ""){          
          let payload = jwt.split(".")[1]
  
          payload = atob(payload)
          payload = JSON.parse(payload).user_data
          if(payload.role == "admin"){
            this.$router.push("/admin")
          }else if(payload.role == "voter"){
            this.$router.push("/voter")
          }
        }
  },
  methods:{
      login(){
          this.$axios.post(`/login`, {nisn:this.nisn, nis:this.nis}).then((data) => {
            if(data.data.status == "ok"){
                  let jwt = data.data.data.token
                  
                  localStorage.setItem("jwt", jwt)
                  
                  let payload = jwt.split(".")[1]

                  payload = atob(payload)
                  payload = JSON.parse(payload).user_data
                  if(payload.role == "admin"){
                    this.$router.push("/admin")
                  }else if(payload.role == "voter"){
                    this.$router.push("/voter")
                  }
                  this.error = ""
              }else{
                  this.error = data.data.errorMsg
              }

          })
      }
  }
}
</script>

