<template>
  <Navbar />
  <div class="pt-14 flex justify-center items-center flex-col h-screen">
    <div class="flex gap-4">
      <div v-for="k in kategori" v-bind:key="k.id">
        <button @click="get(k.id)">{{ k.category_name }}</button>
      </div>
    </div>
    <div>
      <div class="flex gap-4">
        <div v-for="res in result.data" v-bind:key="res.id">
          <div><img v-bind:src="res.candidate_profile_picture" width="200" alt=""></div>
          <div>{{res.full_name}}</div>
          <div>{{res.kelas}}</div>
          <div>{{res.total_suara}} Suara</div>
          <div>{{res.presentase}}%</div>
        </div>
      </div>
      
    </div>
    <div>Presentase Pemilih : {{ (result.suara_masuk / result.total_pemilih) * 100 }}%</div>
  </div>
</template>

<script>
import Navbar from '../components/Navbar.vue'
export default {
  name: "Admin",
  components:{
    Navbar
  },
  data(){
    return {
      kategori:[],
      result: []
    }
  },
  mounted(){
    this.$axios.get("/kategori").then((data) => {
      this.kategori = data.data.data
    })
  },
  methods: {
    get(kategoriID){
      const jwt = localStorage.getItem("jwt")
      this.$axios.get(`/kategori/${kategoriID}/hasil`, { headers: {"Authorization" : `Bearer ${jwt}`}}).then((data) => {
        this.result = data.data.data
      })

    }
  }
}
</script>

