<template>
  <div class="flex bg-indigo-600 w-screen h-12	fixed items-center shadow-xl">
    <div class="w-3/4"></div>
    <div class="w-1/4 items-center flex relative h-full hover:bg-blue-800" @mouseover="() => {this.menu = true}" @mouseleave="() => {this.menu = false}">
      <div class="px-4">

        <div class="flex gap-4 text-white">
          <div>
            <icon :icon="['fas', 'user']" />
          </div>
          
          <div>
            {{ users.full_name }}
          </div>
        </div>
      </div>
      <div class="bg-white flex flex-col w-full text-left shadow-lg top-12 absolute" v-show="menu" @mouseover="() => {this.menu = true}" @mouseleave="() => {this.menu = false}" >
        <div class="hover:bg-blue-500 px-4 py-2 hover:text-white cursor-pointer text-black" @click="() => {this.userInfo = true}">User Info</div>
        <div class="hover:bg-blue-500 px-4 py-2 hover:text-white cursor-pointer text-black" @click="logout()">Logout</div>
      </div>
    </div>
  </div>
  <div class="h-screen w-screen fixed flex flex-col z-10 inset-0 justify-center items-center" v-show="userInfo" >
    <div class="fixed inset-0 bg-black bg-opacity-60"></div>
    <div class="z-10 bg-white w-3/4 h-1/2 relative">
      <button class="absolute right-3 top-3" @click="() => {this.userInfo = false}">X</button>
      <div class="text-left flex gap-4 justify-center items-center h-full">
        <div class="font-bold">
          <div>Name</div>
          <div>NISN</div>
          <div>NIS</div>
          <div>Kelas</div>
        </div>
        <div>
          <div>:</div>
          <div>:</div>
          <div>:</div>
          <div>:</div>
        </div>
        <div>
          <div>{{ users.full_name }}</div>
          <div>{{ users.nisn }}</div>
          <div>{{ users.nis }}</div>
          <div>{{ users.kelas }}</div>
        </div>

      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Navbar",
  data(){
    return {
      users: "",
      menu: false,
      userInfo: false
    }
  },
  mounted(){
    const jwt = localStorage.getItem("jwt")
    let payload = jwt.split(".")[1]
    payload = JSON.parse(atob(payload))
    this.users = payload.user_data
  },
  methods:{
    logout(){
      localStorage.removeItem("jwt")
      this.$router.push("/")
    }
  }
}
</script>

